<script>
  import { fly } from "svelte/transition";

  import { onMount, onDestroy } from "svelte";

  onMount(() => {
    setTimeout(() => {
      document.querySelector("body").classList.add("dark");
    }, 500);
  });

  onDestroy(() => {
    document.querySelector("body").classList.remove("dark");
  });
</script>

<style>
.team {
  height: 100%;
  width: 100%;
  padding: 8% 0 0 50px;
  position: absolute;
}

.team h2 {
  font-size: 50px;
}
</style>

<div class="team" in:fly="{{y: -300, duration: 500, delay: 500}}" out:fly="{{y:-300, duration: 300}}"ы>
  <h2>Команда</h2>
</div>
