<script>
  import { fly } from "svelte/transition";

  import { onMount, onDestroy } from "svelte";

  onMount(() => {
    setTimeout(() => {
      document.querySelector("body").classList.add("light");
    }, 500);
  });

  onDestroy(() => {
    document.querySelector("body").classList.remove("light");
  });
</script>

<style>
.timetable {
  height: 100%;
  width: 100%;
  padding: 8% 0 0 50px;
}

.timetable h2 {
  font-size: 50px;
}
</style>

<div class="timetable" in:fly="{{y: -300, duration: 500, delay: 500}}" out:fly="{{y:-300, duration: 300}}">
  <h2>Расписание</h2>
</div>
